/**
 * System imports
 ============================================================================ */
import React, { lazy } from "react";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import ErrorBoundary from "../errorBoundary";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "FileLoader",
    path1: "components",
    path2: "locale",
    path3: "topics",
    file: null,
    defaultFile: "common-questions.js",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getFileNameFromURI } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    let fileName = props.file !== null ? props.file : getFileNameFromURI();

    let ExternalFile = null;

    ExternalFile =
      fileName !== null
        ? lazy(() =>
            import(
              `../../${props.path1}/${props.path2}/${language}/${props.path3}/${fileName}`
            ).catch(() => {
              return import(
                `../../${props.path1}/${props.path2}/${language}/${props.path3}/${props.defaultFile}`
              );
            })
          )
        : null;

    return <></>;
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
